<template lang="pug">
    div.user-video-content
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            template(v-if="isLoaded")
                svideo-list(:list="list" :allLoaded="allLoaded")
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
</template>
<script>
import nullData from '@/components/nullData'
import svideoList from '@/components/svideoList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
export default {
    name: 'toolSmallVideo',
    components: {
        nullData,
        svideoList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            list: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: false,
            isLoaded: false,
            topStatus: '',
            bottomStatus: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh = false) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/svideoVideo' : this.api.svideoVideo();
            // !isRefresh && Indicator.open();
            this.axios.get(axiosUrl, {
                params: {
                    to_user_id: this.$route.query.to_user_id
                }
            }).then(res => {
                // !isRefresh && Indicator.close();
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.list = res.data.list;
                    this.has_next = res.data.has_next;
                    this.page = 2;
                }
                else {
                    this.Toast(res.data.error || '请求出错');
                }

                this.$nextTick(() => {
                    this.$emit('init');
                    this.isRefresh && this.$refs.loadmore.onTopLoaded();
                });

            })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                    this.$emit('init');
                });
        },
        loadTop(id) {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/svideoVideo' : self.api.svideoVideo();
            if (self.has_next) {
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page,
                        to_user_id: self.$route.query.to_user_id
                    }
                })
                    .then(res => {
                        setTimeout(() => {
                            if (res.data.status == 1) {

                                self.has_next = res.data.has_next;
                                self.page = self.page + 1;

                                if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                    for (let i = 0; i < res.data.list.length; i++) {
                                        self.list.push(res.data.list[i]);
                                    }
                                }
                                
                            }
                            else {
                                this.Toast(res.data.error || '请求出错');
                            }

                            self.$nextTick(() => {
                                self.$refs.loadmore.onBottomLoaded();
                                self.$emit('init');
                            });
                        }, 500);
                    })
                    .catch(err => {
                        this.Toast(err || '网络异常，请求失败');
                    });
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../../assets/css/variable.less";
.user-video-content {
    .nullData {
        margin-top: 10.7rem;
    }
}
</style>




